
.keyboardContainer
    --rot-x: 25deg
    --rot-y: 0deg
    display: flex
    width: 500px
    margin: 0 auto
    flex-direction: column
    align-items: center
    padding: 1rem
    background: #181818
    border-radius: 10px
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5)
    margin-top: 100px
    transform: perspective(1200px) rotateX(var(--rot-x)) rotateY(var(--rot-y))

.keyboardRow
    display: flex
    margin: 5px 0
    width: 500px
    gap: 5px
